<template>
    <div>
        <loader v-if="loading"></loader>
        <h1 v-else>Home page</h1>
        <li v-for="user in users" :key="user.id">
            {{ user.first_name }}
        </li>
    </div>
</template>

<script>
    import msgs from "../utils/messages";
    import Loader from "../components/Loader"

    import axios from 'axios'

    export default {
        name: 'home',
        components: {
            Loader
        },
        data() {
            return {
                todos: [],
                users: [],
                info: null,
                loading: true,
                errored: false
            };
        },
        mounted() {
            if (msgs[this.$route.query.message]) {
                this.$message(msgs[this.$route.query.message])
            }

            this.getData()
        },
        methods: {
            getData() {
                // console.log('getData')

                // this.interval = setInterval(() => {
                axios.get('https://reqres.in/api/users?delay=1')
                    .then(users => {
                        this.todos = users.data
                        this.users = users.data.data
                        // console.log(users.data.data)
                    })
                    .catch(error => {
                        console.log(error);
                        this.errored = true;
                    })
                    .finally(() => (this.loading = false));
                // }, 1000)


                // axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.getters.getToken
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.getters.getToken

                console.log(axios.defaults.headers)
                console.log(axios.defaults.headers.common['Authorization'])
                // console.log('loadData')

                axios.get('//127.0.0.1:8083/api/user/getList')
                    .then(res => {
                        if (res.data.result) {
                            console.log(res.data.result)
                            // self.trailers = res.data.result
                        } else {
                            console.log(res)
                            console.log(res.data.error)
                            this.$store.commit('setError', res.data.error)
                            // self.trailers = res.data.result
                        }
                    })
                    .catch(e => {
                        this.errored = true;
                        this.$store.commit('setError', e)
                        console.log('error- ' + e)
                    })

                axios.get('//127.0.0.1:8083/api/user/getById/' + 1)
                    .then(res => {
                        // console.log(res.data)
                        console.log(res.data.result)
                        console.log(res)
                        // self.trailers = res.data.result
                    })
                    .catch(e => {
                        this.$store.commit('setError', e)
                        this.$toast.success(msgs.auth_success);
                        console.log('error- ' + e)
                    })

                // axios.post('notify/test')
                //         .then(res => {
                //             console.log('notify/test')
                //             console.log(res.data.result)
                //             self.trailers = res.data.result
                //         })
                //         .catch(error => {
                //             console.log(error);
                //             this.errored = true;
                //         })


            }
        }
    }
</script>
